.table-title
{

    display:flex;
    align-items:center;
    justify-content:flex-start;
    margin-bottom:27px;
        &:before
    {
        content:'';
        display:inline-block;
        height:36px;
        width:3px;
        background-color:$mainColor;
        margin-right:32px;
        margin-left:12px;
    }
    h4
    {
        
        font-size:16px;
        margin-bottom:0;
    }
}

.table-products
{
    //margin-left:34px;
    border-left: 34px solid $mainElementBackground;
    tr
    {
        
        th,td
        { 
             border-top:none;
             &:nth-child(n+3)
                {
                text-align: center;
            }
        }
        
        th
        {
            font-size:11px;
            color:$lightTableColor;
           
        }
        
        td
        {
            color:$darkFontColor;
            vertical-align:middle;
            font-size:13px;
            font-weight:600;
            border-bottom:1px solid $tableHrColor;
        
            &.table-photo
            {
                img 
                {
                    width:46px;
                    height:46px;
                } 
            }
            
            
            &.table-price
            {
                color:$lightTableColor;
            }
            
            &.table-promotion
            {
                color:$promotionColor;
            }
            
            &.table-name
            {
                svg 
                {
                    fill:$lightTableColor!important;
                    vertical-align: middle;
                    width:20px;
                    margin-bottom:2px;
                }
                
                a 
                {
                    #{$allStates}
                    {
                        text-decoration:none;
                        color:inherit
                    }

                }
            }
            
            &.table-quanity
            {
                color:$lightTableColor;
                
                input
                {
                    width:52px;
                    height:32px;
                    background-color:$tableInputColor;
                    border:none;
                    text-align:center;
                    color:inherit;
                }

            }
            
           
        
           
            
        }
        
        &:last-of-type
        {
            td
            {
                border-bottom:none;
            }
        }
    }
    
    &:not(:last-of-type)
    {
        margin-bottom:55px;   
    }
    
    
    @include media-breakpoint-down(md)
    {
        margin-left:0;
        th,td
        {
            &:first-of-type,&:nth-of-type(3),&:nth-of-type(4),&:nth-of-type(5)
                {
             display:none;   
            }
            
        }
        
        border-left: 10px solid $mainElementBackground;
    }
    
}