.nav-main
{
    width:250px;
    position:fixed;
    height:100vh;
    left:0;
    top:0;
    padding-top:80px;
    background-color:$darkBackgroundColor;
    color:#8a95a1;
    overflow-y:auto;
    z-index:20;
    
    @include media-breakpoint-down(sm)
    {
        transform:translateX(-100%);
        transition: transform 300ms;
        &.nav-showed
        {
            transform:none;
        }
    }
    
    li svg *
    {
       fill:$lightTableColor!important; 
    }
    
    a
    {
        #{$allStates}
        {
            color:$lightTableColor;
            text-decoration: none; 
        }
            

    }
    
    .category-title
    {
        padding-left:20px;
    }
    
    .section-name
    {
        font-size:12px;
        padding-left:20px;
        margin-top:30px;
    }
    
    ul
    {
        list-style:none;
        padding-left:0;
        &>li
        {

            font-size:14px;
            font-weight:600;
            margin-top:15px;
            box-sizing:border-box;
            border-left:3px solid transparent;
            .category-title
            {
                display: flex;
                justify-content: flex-start;
                align-items: center; 
                
                .container-category-icon
                {
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;   
                    width:30px;
                    img
                    {
                       // height:14px;
                    }
                }
                
                &>.menu-dropdown
                {
                    margin-left:auto;
                    padding:8px;
                    margin-right:11px;
                }
            }
            &>ol
            {
                padding-left:50px;
                list-style:none;
                margin-bottom:15px;
                padding-bottom:15px;
                display:none;
                &>li>a
                {
                        padding-left:20px;
                }
                
                &>li
                {
                   position:relative;
                   z-index:30;
                   margin-top:3px;
                   margin-bottom:3px;
                   &>div
                   {
                        @extend .d-flex;
                        @extend .justify-content-between;
                        padding-right:20px;
                   }

                }
                
                &>li.active
                {

                   /*&:before
                       {
                       content:'';
                       display:block;
                       width:100%;
                       height:28px;
                       position:absolute;
                       top:50%;
                       left:-28px;
                       padding-right:28px;
                       box-sizing: content-box;
                       transform:translateY(-50%);
                       background-color:$darkBackgroundColor; 
                       z-index:-1;
                   }*/
                   
                   &>div>a
                   {
                       color:$mainColor;
                   }
                }
                
                ol
                {
                    list-style: none;
                    display:none;
                    padding-left:20px;
                    
                    li
                    {
                        margin-top:5px;
                        margin-bottom:5px;
                                           &>div
                        {
                             @extend .d-flex;
                             @extend .justify-content-between;
                             padding-right:20px;
                        }
                        
                        &.active>div>a
                        {
                            color:$mainColor;
                        }
                        
                    }
                    
                }
            }
            
            &.active
            {
                border-left:3px solid $mainColor; 
                &>a
                {
                //    background-color:$darkBackgroundColor;
                }
            }
        }
        
        li.active
        {
            &>a, .category-title>a
            {
                color:$mainColor;
            }
            
            
            svg *
            { 
                fill:$mainColor!important;
            }
        }
        
        &:nth-of-type(2)
        {
            background-color:$darkerBackgroundColor;
            padding-bottom:40px;
            position:relative;
            z-index:50;
            &>li:first-of-type>.category-title
            {
                padding-top:12px;

            }
            
            &>li>ol
            {
                padding-top:7px;
            }
        }
    }
    
    .mCSB_container.mCS_no_scrollbar_y.mCS_y_hidden
    {
        min-height:100%;
    }
    
    .mCSB_container>img,&>img
    {
        position:absolute;
        bottom:35px;
        left:30px;
    }
    
    .mCSB_scrollTools .mCSB_draggerContainer
    {
        z-index:60;
    }
    
    /*#mCSB_1_scrollbar_vertical
    {
        transform:translateX(100%);
    }*/
}