/*

z-index ranges:
1-99 : menu on the left
100-199 : navbar at the top

*/

/*Project variables*/
$darkBackgroundColor:#232e3d;
$darkerBackgroundColor:#19222d;
$mainColor:#ff3d2e;
//$mainColor:#088ab6;
$alertColor:$mainColor;
$darkFontColor:#4c5968;
$lightFontColor:#748190;
$headerFontColor:#fff;
$lightBackground:#f3f6f9; 
$mainElementBackground:#fff;
$promotionColor:#007bd9;
$lightTableColor:#8a95a1;
$tableHrColor:#f1f7f9;
$tableInputColor:#ecf0f1;
$secondaryButtonBackground:#91969e;
$allStates:'&,&:hover,&:active,&:focus,&:focus:active';
$historyColor:#f1f7f9;

/*Bootstrap changes*/
$enable-rounded:false;
$input-box-shadow:none;
$input-focus-box-shadow:none;
$input-focus-border-color:$mainColor;
$btn-box-shadow:   none;
$input-btn-focus-box-shadow:none;
@import "bootstrap/bootstrap";
@import "jquery.mCustomScrollbar";
@import "jquery-ui.min";
@import "fonts";
//@import "jquery-ui.theme.min";


body
{
    background-color:$lightBackground!important;
    overflow-y: scroll;
    
    font-family: "metropolisregular",Fallback,sans-serif;
}

button
{
    #{$allStates}
    {
     cursor:pointer;
    }
    
}

input,button
{
        #{$allStates}
    {
     outline:0;
    }
    
}

.form-error
{
    color:$alertColor!important;
    font-size:13px;
}

.btn
{
    background-color:$mainColor;
    display:flex;
    align-items:center;
    border:none;
    svg
    {
        margin-right:9px; 
        *
        {
          fill:#fff!important;  
        }
         
        
    }
    #{$allStates}
    {
        color:#fff;
    }
}

.input-custom
{
    background-color:$tableInputColor;
    border:none;
    text-align:center;
    color:inherit;
    resize: none;
}

#notification
{
    .modal-footer button
    {
        border:none;
        
        
        #{$allStates}
        {
            background-color:$mainColor;
        }
    }
}

a.link
{
    #{$allStates}
    {
         color:inherit;
        text-decoration: none;   
    }

}

.button-secondary
{
    background-color:$secondaryButtonBackground;
    
    padding:7px 23px;
    #{$allStates}
    {
       color:$headerFontColor;
       text-decoration: none;
    }
}

@import "login";
@import "nav"; 
@import "header";
@import "content"; 
@import "table";
@import "product";
@import "lightbox";
@import "account";
@import "order";
@import "order-history";